@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-12{
          p{
            padding: 0 60px 0 0;
            font: 3.438em/105px "BravoSC";
            color: #d10000;
            text-align: right;
            text-transform: uppercase;
            background-image: url("../img/vh_logo_large.jpg");
            background-size: cover;
          }
        }
      }
      .row{
        .col-lg-3{
          .aside{
            &__vhLogo{
              display: none;
            }
          }
        }
        .col-lg-9{
          .customize{
            margin: 30px 0;
            &__headline{
              p{
                font: .875em/1.25em "Arial";
                color: #525252;
              }
            }
            &__firstStep{
              .itemsShape{
                margin: 20px 0;
                border: 1px solid #d0d0d0;
                h5{
                  padding: 0 0 0 25px;
                  font: 1.25em/40px "Arial";
                  font-weight: bold;
                  text-transform: uppercase;
                  background-color: #eaeaea;
                }
                ul{
                  margin: 15px 0;
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  li{
                    span{
                      width: 160px;
                      display: block;
                      background-color: #f8f8f8;
                      border: 1px solid #c7c7c7;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                    &:nth-child(1),
                    &:nth-child(2){
                      span{
                        height: 160px;
                      }
                    }
                    &:nth-child(2){
                      span{
                        border-radius: 50%;
                      }
                    }
                    &:nth-child(3),
                    &:nth-child(4){
                      span{
                        height: 100px;
                      }
                    }
                    &:nth-child(4){
                      span{
                        border-radius: 50%;
                      }
                    }
                    .active{
                      background-color: #ececec;
                      border-color: #32d5fd;
                    }
                  }
                }
              }
              .itemsSize{
                margin: 20px 0;
                display: flex;
                flex-wrap: wrap;
                border: 1px solid #d0d0d0;
                h5{
                  padding: 0 0 0 25px;
                  width: 100%;
                  font: 1.25em/40px "Arial";
                  font-weight: bold;
                  text-transform: uppercase;
                  background-color: #eaeaea;
                }
                ul{
                  display: flex;
                  li{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    h6{
                      margin: 0 20px 0 0;
                      font: 1.063em/1.188em "Arial";
                      text-transform: uppercase;
                    }
                    label{
                      span{
                        color: #515151;
                      }
                    }
                    select{
                      margin: 0 0 0 20px;
                      padding: 0 0 0 10px;
                      width: 70px;
                      height: 30px;
                      font: 1.063em/30px "Arial";
                      color: #515151;
                      background-color: #f8f8f8;
                      outline: none;
                      option{
                        font: 1.063em/30px "Arial";
                        color: #515151;
                        background-color: #f8f8f8;
                      }
                    }
                  }
                }
                .width{
                  margin: 20px 25px 20px 20px;
                }
                .height{
                  margin: 20px 20px 20px 25px;
                }
              }
              .itemsBase{
                margin: 20px 0;
                border: 1px solid #d0d0d0;
                h5{
                  padding: 0 0 0 25px;
                  font: 1.25em/40px "Arial";
                  font-weight: bold;
                  text-transform: uppercase;
                  background-color: #eaeaea;
                }
                ul{
                  display: flex;
                  li{
                    display: flex;
                    align-items: center;
                    p{
                      width: 65px;
                      height: 65px;
                      display: inline-block;
                      border: 1px solid #c7c7c7;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                    span{
                      margin: 0 0 0 15px;
                      font: 1.063em/1.188em "Arial";
                      color: #525252;
                    }
                    .silver{
                      background: linear-gradient(to top left, #979897, #cdcdcd);
                    }
                    .gold{
                      background: linear-gradient(to top left, #dfa700, #fdd300);
                    }
                    &:first-child{
                      margin: 20px 25px 20px 20px;
                    }
                    &:last-child{
                      margin: 20px 20px 20px 25px;
                    }
                  }
                }
              }
              .forward{
                display: flex;
                justify-content: center;
                a{
                  margin: 30px 0 40px;
                  width: 215px;
                  display: block;
                  font: 1.063em/40px "Arial";
                  color: #fff;
                  text-align: center;
                  text-transform: uppercase;
                  text-decoration: none;
                  background-color: #009fff;
                }
              }
            }
            &__secondStep{
              display: flex;
              align-items: center;
              background-color: #ededed;
              border: 1px solid #d0d0d0;
              .hologram{
                padding: 10px;
                width: 50%;
                ul{
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  li{
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    .arrowUp,
                    .arrowDown{
                      width: 14px;
                      height: 75px;
                      display: block;
                    }
                    .arrowUp{
                      margin: 0 0 45px;
                      background: url("../img/arrow_up.png") no-repeat;
                      background-size: cover;
                    }
                    .arrowDown{
                      margin: 45px 0 0;
                      background: url("../img/arrow_down.png") no-repeat;
                      background-size: cover;
                    }
                    .hologramPreview{
                      width: 90%;
                      height: 355px;
                      position: relative;
                      overflow: hidden;
                      img{
                        width: 100%;
                        position: absolute;
                        top: 0;
                        box-shadow: 5px 5px 15px #777;
                      }
                      .layer-1{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-wrap: nowrap;
                        justify-content: flex-start;
                        align-content: space-around;
                        position: absolute;
                        top: 0;
                        z-index: 5;
                        li{
                          min-height: 40px;
                          display: flex;
                          position: relative;
                          span{
                            margin: 0 10px;
                            display: inline-block;
                            font: 1.75em/1.4em "Arial";
                            white-space: nowrap;
                            text-transform: uppercase;
                          }
                        }
                        .companyName{
                          left: -185px;
                          span{
                            font-weight: bold;
                            color: #18ff00;
                          }
                        }
                        .security{
                          left: -25px;
                          span{

                            color: #ff0000;
                          }
                        }
                      }
                      .layer-2{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        flex-wrap: nowrap;
                        justify-content: flex-start;
                        align-content: space-around;
                        position: absolute;
                        top: 0;
                        transform: rotate(-45deg);
                        z-index: 10;
                        li{
                          margin: 30px 0;
                          display: flex;
                          flex-wrap: nowrap;
                          position: relative;
                          top: -65px;
                          span{
                            margin: 0 25px;
                            display: inline-block;
                            font: 1.25em/1.5em "Arial";
                            color: #002aff;
                            white-space: nowrap;
                          }
                          &:nth-child(odd){
                            margin: 30px 0 30px 100px;
                          }
                        }
                      }
                      .layer-3{
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        z-index: 15;
                        .bg{
                          width: 285px;
                          height: 140px;
                          display: block;
                          position: relative;
                          background-color: #fff;
                          ul{
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-direction: row;
                            li{
                              width: 50%;
                              display: flex;
                              justify-content: center;
                              align-items: center;
                            }
                            .userLogo{
                              img{
                                margin: 10px;
                                width: 105px;
                                height: 115px;
                                position: unset;
                                left: 5px;
                                box-shadow: none;
                              }
                            }
                            .userText{
                              span{
                                padding: 0 0 0 5px;
                                display: inline-block;
                                font: 2.25em/1.25em "Arial";
                                font-weight: bold;
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
              .hologramOptions{
                width: 50%;
                .layer-1,
                .layer-2,
                .layer-3{
                  h6{
                    margin: 15px 0;
                    padding: 0 0 0 15px;
                    font: 1.25em/35px "Arial";
                    font-weight: bold;
                    background-color: #d9d9d9;
                  }
                  .companyName,
                  .companySecurity,
                  .companyOriginal,
                  .companyTop{
