                        width: 220px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        color: #000;
                        border: 1px solid #d0d0d0;
                      }
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
                      height: 40px;
                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                    .color{
                      width: 150px;
                      input{
                        margin: 0 5px 0 0;
                        width: 120px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        color: #000;
                        text-align: center;
                        border: 1px solid #d0d0d0;
                      }
                    }
                    .fontSize{
                      width: 175px;
                      display: flex;
                      align-items: center;
                      p{
                        font: 1.063em/40px "Arial";
                      }
                      input{
                        margin: 0 5px 0 8px;
                        width: 40px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        text-align: center;
                        text-transform: uppercase;
                        border: 1px solid #d0d0d0;
                      }
                      span{
                        display: flex;
                        flex-direction: column;
                        i{
                          color: #427b94;
                          &:hover{
                            cursor: pointer;
                          }
                        }
                      }
                    }
                    .fontWeight{
                      display: flex;
                      align-items: center;
                      p{
                        margin: 0 8px 0 0;
                        font: 1.063em/40px "Arial";
                      }
                      input[type="checkbox"]{
                        display: none;
                      }
                      input[type="checkbox"] + span:before{
                        font-family: "FontAwesome";
                      }
                      input[type="checkbox"]:checked + span:before{
                        content: "\f111";
                        font-size: 10px;
                        line-height: 20px;
                        color: #6d6d6d;
                      }
                      label{
                        width: 20px;
                        height: 20px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: #fff;
                        border: 1px solid #d0d0d0;
                        border-radius: 50px;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                  .companyName{
                    .color{
                      input{
                        color: #000;
                        background-color: #07ff00;
                      }
                      input::-webkit-input-placeholder{
                        color: #000;
                      }
                      input:-moz-placeholder{
                        color: #000;
                      }
                    }
                  }
                  .companySecurity{
                    .color{
                      input{
                        color: #fff;
                        background-color: #ff0000;
                      }
                      input::-webkit-input-placeholder{
                        color: #fff;
                      }
                      input:-moz-placeholder{
                        color: #fff;
                      }
                    }
                  }
                  .companyOriginal{
                    .color{
                      input{
                        color: #fff;
                        background-color: #002aff;
                      }
                      input::-webkit-input-placeholder{
                        color: #fff;
                      }
                      input:-moz-placeholder{
                        color: #fff;
                      }
                    }
                  }
                  .companyTop{
                    .color{
                      input{
                        color: #000;
                        background-color: #fff;
                      }
                      input::-webkit-input-placeholder{
                        color: #000;
                      }
                      input:-moz-placeholder{
                        color: #000;
                      }
                    }
                  }
                  .selectFile{
                    padding: 7.5px 0 15px;
                    display: flex;
                    input{
                      width: 0.01px;
                      height: 0.01px;
                      opacity: 0;
                      overflow: hidden;
                      z-index: -1;
                    }
                    input + label{
                      margin: 0 65px 0 0;
                      min-width: 160px;
                      display: block;
                      font: 1.063em/40px "Arial";
                      color: #fff;
                      text-align: center;
                      text-decoration: none;
                      background-color: #427b94;
                    }
                    input:focus + label,
                    input + label:hover{
                      background-color: #009fff;
                    }
                    input + label{
                      cursor: pointer;
                    }
                    input:focus + label{
                      outline: 1px solid #fff;
                      outline: -webkit-focus-ring-color auto 5px;
                    }
                    input + label * {
                      pointer-events: none;
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
                      height: 40px;
                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                }
              }
            }
